import React from 'react';
import UniversalShapeContext from './UniversalShapeContext';
import Highscore from './Highscore';
import CM from './ClientManager';
import AutoComplete from './AutoComplete';
import UserInfo from './UserInfo';
import { TimelineMax } from 'gsap';

class Menu extends React.Component<{}, {
    user: any; width: number; display: number; 
    difficulty: number; highscore: any[]; highscoreEasy: any[];
    showAnimation: boolean;
}> {

    buttons = document.getElementsByClassName('menuButton');

    constructor(props: any) {
        super(props);
        this.state = {
            user: null,
            display: 0,
            difficulty: 7,
            highscore: [],
            highscoreEasy: [],
            width: window.innerWidth,
            showAnimation: true
        }
    }
    componentDidMount = () => {
        CM.initMenu(this.setHighscore);
        CM.emitVerifyUser('', '', this.setUser);
        this.setState({ width: window.innerWidth })
        // change MySQL connetion first

    }

    setUserDetails = (user: any) => {
        CM.emitUserConnected(user);
        this.setState({ user, showAnimation: user.showAnimation })
    }

    setUser = ({ user, isUser }: any) => {
        if (isUser == 0) {
            // this.setError('User is already logged in!');
        }
        else if (isUser == 1) {
            //  this.setError('Incorrect username or password!');
        }
        else if (isUser == 2) {
            this.setUserDetails(user);
            //  this.setError('');
        }
    }

    animate = () => {
        let tm = new TimelineMax();
        tm.fromTo(this.buttons, 1, { opacity: 0 }, { opacity: '1' }, '-=0.2');
    }
    setDisplay = (event: any) => {
        const value = event.target.value * 1
        if (value == 2) {
            CM.emitMultiplayer(this.state.user.name)
        }
        this.setState({
            display: event.target.value * 1
        })

    }

    setHighscore = (highscore: any) => {
        highscore.mode == 'normal' ? this.setState({ highscore: highscore.result }) : this.setState({ highscoreEasy: highscore.result });

    }

    setDifficulty = (event: any) => {
        this.setState({
            difficulty: event.target.value * 1,
            display: 0
        });
        const {difficulty, showAnimation} = this.state;
        CM.emitSetting(difficulty, showAnimation);
    }

    setAnimation = (event: any) => {
        const showAnimation = event.target.value * 1 ? true : false;
        this.setState({
            showAnimation,
            display: 0
        });
        const {difficulty} = this.state;
        CM.emitSetting(difficulty, showAnimation);
    }

    logout = () => {
        CM.emitLogout();
    }

    render() {
        const { user, display, difficulty, highscore, highscoreEasy, width, showAnimation } = this.state;
        let show = <div></div>;
        switch (display) {
            case 0:
                show =

                    <div className="leftMenu">
                        <p className='title2'>TETRIS</p>

                        <div className="menuButtons">

                            <p>Menu</p>
                            <button className="menuButton" onClick={this.setDisplay} value={1}>SINGLEPLAYER</button>
                            <button className="menuButton" onClick={this.setDisplay} value={2}>MULTIPLAYER</button>
                            <button className="menuButton" onClick={this.setDisplay} value={3}>SPECTATE</button>
                            <button className="menuButton" onClick={this.setDisplay} value={4}>SETTINGS</button>
                            <button className="menuButton" onClick={this.setDisplay} value={5}>HIGHSCORES</button>
                            <button className="menuButton" onClick={this.setDisplay} value={6}>ANIMATION</button>
                        </div>
                    </div>

                    ; break;
            case 1:
                show = <UniversalShapeContext setDisplay={this.setDisplay} difficulty={difficulty} user={user} mode={display} showAnimation={showAnimation}/>;

                ; break;
            case 2:
                show = <UniversalShapeContext setDisplay={null} difficulty={difficulty} user={user} mode={display} showAnimation={showAnimation}/>
                    ; break;
            case 3:
                show = <UniversalShapeContext setDisplay={null} difficulty={difficulty} user={user} mode={display} showAnimation={showAnimation}/>
                    ; break;
            case 4:
                show =
                    <div className="leftMenu">
                        <p className='title2'>TETRIS</p>
                        <div className="menuButtons">
                            <p>Menu</p>
                            <button className="menuButton" onClick={this.setDisplay} value={7}>DIFFICULTY</button>
                            <button className="menuButton" onClick={this.setDisplay} value={8}>ANIMATION OPTIONS</button>
                            <button onClick={this.setDisplay} value={0}>Back</button>
                        </div>
                    </div>
                    ; break;
            case 5:
                show = <div className="highscoreWrapper">

                    {highscore != [] ? <Highscore scores={highscore} title={"Normal"} /> : null}
                    {highscoreEasy != [] ? <Highscore scores={highscoreEasy} title={"Easy"} /> : null}
                    <button onClick={this.setDisplay} value={0}>Back</button>
                </div>
                ; break;
            case 6: 
            show = <AutoComplete
            rows={20}
            columns={20}
            blockSize={40}
        />; break;
            case 7:
            show =
            <div className="leftMenu">
                <p className='title2'>TETRIS</p>
                <div className="menuButtons">
                    <p>Select difficulty</p>
                    <button onClick={this.setDifficulty} value={7}>NORMAL</button>
                    <button onClick={this.setDifficulty} value={10}>EASY</button>
                    <button onClick={this.setDisplay} value={4}>Back</button>
                </div>
            </div>
            ; break;
            case 8:
            show =
            <div className="leftMenu">
                <p className='title2'>TETRIS</p>
                <div className="menuButtons">
                    <p>Animation</p>
                    <button onClick={this.setAnimation} value={1}>SHOW</button>
                    <button onClick={this.setAnimation} value={0}>HIDE</button>
                    <button onClick={this.setDisplay} value={4}>Back</button>
                </div>
            </div>
            ; break;
        }


        return (
            <div className='mainWrapper'>
                {user ? <UserInfo user={user.name} logout={this.logout} setDisplay={this.setDisplay} gameMode={display} /> : null}
                <div className='menuWrapper'>
                    {show}
                    {(display == 0 || display == 4 || display == 8 || display == 7) ?
                        <div className="homepageTextContainer">
                        <h2>Singleplayer</h2>
                        <p>No people, no queues, no waiting, just the plain version of the game Tetris ready to be played. The difficulty can be adjusted in settings with Normal and Easy modes available.</p>
                        <h2>Multiplayer</h2>
                        <p>Designed for people who like a bit of competition. Invite your friends or other people to a game of Tetris. 
                            Be careful though, the game becomes faster based on the highest player score in your group.</p>
                        <h2>Spectate</h2>
                        <p>Achieving the highest score is too stressful? Relax and watch other people play. Just select your desired game.</p>
                    </div> : null}
                </div>
            </div>
        )
    }
}

export default Menu;